// panel.questionTitleLocation: "Applies to all questions within this panel. If you want to override this setting, define title alignment rules for individual questions. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Plat pro vechny otzky v rmci tohoto panelu. Chcete-li toto nastaven pepsat, definujte pravidla zarovnn nadpis pro jednotliv otzky. Monost \"Zddit\" pouije nastaven na rovni strnky (pokud je nastaveno) nebo na rovni przkumu (ve vchozm nastaven \"Nahoe\")."
// panel.questionErrorLocation: "Sets the location of an error message in relation to all questions within the panel. The \"Inherit\" option applies the page-level (if set) or survey-level setting." => "Nastav umstn chybov zprvy ve vztahu ke vem otzkm v panelu. Monost \"Zddit\" pouije nastaven na rovni strnky (pokud je nastaveno) nebo na rovni przkumu."
// panel.questionOrder: "Keeps the original order of questions or randomizes them. The \"Inherit\" option applies the page-level (if set) or survey-level setting." => "Zachov pvodn poad otzek nebo je nhodn vybere. Monost \"Zddit\" pouije nastaven na rovni strnky (pokud je nastaveno) nebo na rovni przkumu."
