// panel.startWithNewLine: "Unselect to display the panel in one line with the previous question or panel. The setting doesn't apply if the panel is the first element in your form." => "Zrute vbr, chcete-li panel zobrazit v jednom dku s pedchoz otzkou nebo panelem. Nastaven se nepouije, pokud je panel prvnm prvkem ve formuli."
// panel.state: "Choose from: \"Expanded\" - the panel is displayed in full and can be collapsed; \"Collapsed\" - the panel displays only the title and description and can be expanded; \"Locked\" - the panel is displayed in full and cannot be collapsed." => "Vyberte si z: \"Rozbalen\" - panel se zobraz cel a lze jej sbalit; \"Sbaleno\" - panel zobrazuje pouze nzev a popis a lze jej rozbalit; \"Zameno\" - obraz je zobrazen cel a nelze jej sbalit."
// panel.width: "Sets the width of the panel in proportion to other survey elements in the same line. Accepts CSS values (px, %, in, pt, etc.)." => "Nastav ku panelu v pomru k ostatnm prvkm zamen ve stejnm dku. Pijm hodnoty CSS (px, %, in, pt atd.)."
