// paneldynamic.enableIf: "Use the magic wand icon to set a conditional rule that disables the read-only mode for the panel." => "Pomoc ikony kouzeln hlky nastavte podmnn pravidlo, kter pro panel zake reim jen pro ten."
// paneldynamic.requiredIf: "Use the magic wand icon to set a conditional rule that prevents survey submission unless at least one nested question has an answer." => "Pomoc ikony kouzeln hlky nastavte podmnn pravidlo, kter zabrn odesln przkumu, pokud alespo jedna vnoen otzka nem odpov."
// paneldynamic.templateQuestionTitleLocation: "Applies to all questions within this panel. If you want to override this setting, define title alignment rules for individual questions. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Plat pro vechny otzky v rmci tohoto panelu. Chcete-li toto nastaven pepsat, definujte pravidla zarovnn nadpis pro jednotliv otzky. Monost \"Zddit\" pouije nastaven na rovni strnky (pokud je nastaveno) nebo na rovni przkumu (ve vchozm nastaven \"Nahoe\")."
// paneldynamic.templateErrorLocation: "Sets the location of an error message in relation to a question with invalid input. Choose between: \"Top\" - an error text is placed at the top of the question box; \"Bottom\" - an error text is placed at the bottom of the question box. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Nastav umstn chybov zprvy ve vztahu k otzce s neplatnm vstupem. Vyberte si mezi: \"Nahoe\" - text chyby je umstn v horn sti pole s otzkou; \"Bottom\" - text chyby je umstn ve spodn sti pole s otzkou. Monost \"Zddit\" pouije nastaven na rovni strnky (pokud je nastaveno) nebo na rovni przkumu (ve vchozm nastaven \"Nahoe\")."
// paneldynamic.errorLocation: "Sets the location of an error message in relation to all questions within the panel. The \"Inherit\" option applies the page-level (if set) or survey-level setting." => "Nastav umstn chybov zprvy ve vztahu ke vem otzkm v panelu. Monost \"Zddit\" pouije nastaven na rovni strnky (pokud je nastaveno) nebo na rovni przkumu."
// paneldynamic.page: "Repositions the panel to the end of a selected page." => "Pemst panel na konec vybran strnky."
// paneldynamic.innerIndent: "Adds space or margin between the panel content and the left border of the panel box." => "Pid mezeru nebo okraj mezi obsah panelu a lev okraj rmeku panelu."
// paneldynamic.startWithNewLine: "Unselect to display the panel in one line with the previous question or panel. The setting doesn't apply if the panel is the first element in your form." => "Zrute vbr, chcete-li panel zobrazit v jednom dku s pedchoz otzkou nebo panelem. Nastaven se nepouije, pokud je panel prvnm prvkem ve formuli."
// paneldynamic.state: "Choose from: \"Expanded\" - the panel is displayed in full and can be collapsed; \"Collapsed\" - the panel displays only the title and description and can be expanded; \"Locked\" - the panel is displayed in full and cannot be collapsed." => "Vyberte si z: \"Rozbalen\" - panel se zobraz cel a lze jej sbalit; \"Sbaleno\" - panel zobrazuje pouze nzev a popis a lze jej rozbalit; \"Zameno\" - obraz je zobrazen cel a nelze jej sbalit."
// paneldynamic.width: "Sets the width of the panel in proportion to other survey elements in the same line. Accepts CSS values (px, %, in, pt, etc.)." => "Nastav ku panelu v pomru k ostatnm prvkm zamen ve stejnm dku. Pijm hodnoty CSS (px, %, in, pt atd.)."
// paneldynamic.templateTitle: "Type in a template for dynamic panel titles. Use {panelIndex} for the panel's general position and {visiblePanelIndex} for its order among visible panels. Insert these placeholders into the pattern to add automatic numbering." => "Zadejte ablonu pro dynamick nzvy panel. Pouijte {panelIndex} pro obecnou pozici panelu a {visiblePanelIndex} pro jeho poad mezi viditelnmi panely. Vloenm tchto zstupnch symbol do vzorku pidte automatick slovn."
// paneldynamic.templateTabTitle: "Type in a template for tab titles. Use {panelIndex} for a panel's general position and {visiblePanelIndex} for its order among visible panels. Insert these placeholders into the pattern to add automatic numbering." => "Zadejte ablonu pro nzvy karet. Pouijte {panelIndex} pro obecnou pozici panelu a {visiblePanelIndex} pro jeho poad mezi viditelnmi panely. Vloenm tchto zstupnch symbol do vzorku pidte automatick slovn."
// paneldynamic.templateVisibleIf: "This setting allows you to control the visibility of individual panels within the dynamic panel. Use the `{panel}` placeholder to reference the current panel in your expression." => "Toto nastaven umouje dit viditelnost jednotlivch panel v rmci dynamickho panelu. Pomoc zstupnho symbolu {panel} mete odkazovat na aktuln panel ve vrazu."
// paneldynamic.titleLocation: "This setting is automatically inherited by all questions within this panel. If you want to override this setting, define title alignment rules for individual questions. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Toto nastaven je automaticky pevzato vemi otzkami v tomto panelu. Chcete-li toto nastaven pepsat, definujte pravidla zarovnn nadpis pro jednotliv otzky. Monost \"Zddit\" pouije nastaven na rovni strnky (pokud je nastaveno) nebo na rovni przkumu (ve vchozm nastaven \"Nahoe\")."
// paneldynamic.descriptionLocation: "The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Under the panel title\" by default)." => "Monost \"Zddit\" pouije nastaven na rovni strnky (pokud je nastaveno) nebo na rovni przkumu (ve vchozm nastaven \"Pod nzvem panelu\")."
// paneldynamic.newPanelPosition: "Defines the position of a newly added panel. By default, new panels are added to the end. Select \"Next\" to insert a new panel after the current one." => "Definuje polohu nov pidanho panelu. Ve vchozm nastaven se nov panely pidvaj na konec. Vbrem monosti \"Dal\" vlote nov panel za aktuln."
// paneldynamic.copyDefaultValueFromLastEntry: "Duplicates answers from the last panel and assigns them to the next added dynamic panel." => "Duplikuje odpovdi z poslednho panelu a piad je dalmu pidanmu dynamickmu panelu."
// paneldynamic.keyName: "Reference a question name to require a user to provide a unique response for this question in each panel." => "Odkazujte na nzev otzky, chcete-li vyadovat, aby uivatel na tuto otzku v kadm panelu poskytl jedinenou odpov."
// pehelp.defaultValueExpression: "This setting allows you to assign a default answer value based on an expression. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression serves as the initial default value that can be overridden by a respondent's manual input." => "Toto nastaven umouje piadit vchoz hodnotu odpovdi na zklad vrazu. Vraz me obsahovat zkladn vpoty - '{q1_id} + {q2_id}', logick vrazy, napklad '{age} > 60', a funkce: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()' atd. Hodnota uren tmto vrazem slou jako poten vchoz hodnota, kterou lze pepsat runm zadnm respondenta."
// pehelp.resetValueIf: "Use the magic wand icon to set a conditional rule that determines when a respondent's input is reset to the value based on the \"Default value expression\" or \"Set value expression\" or to the \"Default answer\" value (if either is set)." => "Pomoc ikony kouzeln hlky mete nastavit podmnn pravidlo, kter uruje, kdy se vstup respondenta resetuje na hodnotu na zklad \"Vrazu vchoz hodnoty\" nebo \"Vrazu nastavit hodnotu\" nebo na hodnotu \"Vchoz odpov\" (pokud je nastavena kterkoli z nich)."
// pehelp.setValueIf: "Use the magic wand icon to set a conditional rule that determines when to run the \"Set value expression\" and dynamically assign the resulting value as a response." => "Pomoc ikony kouzeln hlky mete nastavit podmnn pravidlo, kter uruje, kdy se m spustit vraz \"Nastavit hodnotu\", a dynamicky piadit vslednou hodnotu jako odpov."
// pehelp.setValueExpression: "Specify an expression that defines the value to be set when the conditions in the \"Set value if\" rule are met. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression can be overridden by a respondent's manual input." => "Zadejte vraz, kter definuje hodnotu, kter m bt nastavena, kdy jsou splnny podmnky v pravidle \"Nastavit hodnotu, pokud\". Vraz me obsahovat zkladn vpoty - '{q1_id} + {q2_id}', logick vrazy, napklad '{age} > 60', a funkce: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()' atd. Hodnota uren tmto vrazem me bt pepsna runm zadnm respondenta."
