// pehelp.choicesFromQuestionMode: "Choose from: \"All\" - copies all choice options from the selected question; \"Selected\" - dynamically copies only selected choice options; \"Unselected\" - dynamically copies only unselected choice options. The \"None\" and \"Other\" options are copied by default if enabled in the source question." => "Vyberte si z: \"Ve\" - zkopruje vechny monosti vbru z vybran otzky; \"Vybran\" - dynamicky kopruje pouze vybran monosti volby; \"Nevybrno\" - dynamicky zkopruje pouze nevybran monosti vbru. Monosti \"dn\" a \"Jin\" jsou ve vchozm nastaven zkoprovny, pokud jsou povoleny ve zdrojov otzce."
